import React, { Component } from 'react'

export class FooterContent extends Component {

    render() {

        return (

            <div id="footer-content">
                <p className="center-paragraph" id="footer-paragraph">Rodrigo Rocha</p>
                <p className="center-paragraph" id="footer-paragraph">{this.getYear()}</p>
            </div>
        )
    }

    getYear() {
        let today = new Date();
        return today.getFullYear();
    }
}

export default FooterContent
